export default {
  data() {
    return {
      data: {
        code: '',
        key: ''
      },
      loading: false,
      countDown: 180,
      end: false
    }
  }
  ,
  methods: {
    async confirm() {
      this.loading = true;
      this.data.key = localStorage.getItem('key');
      if (this.data.code.trim() === '') {
        this.loading = false;
        return this.$notify({title: 'لطفا کد تایید را وارد کنید.'});

      }
      if (this.data.code.length !== 5) {
        this.loading = false;
        return this.$notify({title: 'لطفا کد تایید را به درستی وارد کنید.'});
      }
      this.data.key = this.digitConverter(this.data.key)
      this.data.code = this.digitConverter(this.data.code)

      const log = await this.$axios.post('confirm_code', this.data);
      if (log.status === 200) {
        this.loading = false;
        return this.$notify({
          type: 'success',
          title: log.data.msg
        });
      }
      if (log.status === 201) {
        localStorage.setItem('117115101114', JSON.stringify(log.data.user));
        localStorage.setItem('116111107101110', log.data.token);
        this.$router.go(-1);
      }
      // console.log(log.status)
    },
    countDownTimer() {
      if (this.countDown > 0) {
        setTimeout(() => {
          this.countDown -= 1
          this.countDownTimer()
        }, 1000)
      }
      if (this.countDown === 0) this.end = true
    },
    async sendCode() {
      const dataa = {
        key: this.digitConverter(localStorage.getItem('key'))
      };
      const log = await this.$axios.post('login_or_reg', dataa);
      if (log.data === 0) {
        this.$store.dispatch('setNewUser', true)
      }

      if (log.data === 1) {
        this.$store.dispatch('setNewUser', false)
      }
      if (log.data.email) {
        this.loading = false;
        return this.$notify({title: 'خطا در ارسال کد.  لطفا مجددا وارد شوید.'});
      }
      localStorage.setItem('key', this.data.key);
      this.$notify({
        type: 'success',
        title: 'کد تایید به شما ارسال شد.'
      });
      this.$store.dispatch('setFlag', true);
      this.end = false;
      this.countDown = 8;
      this.countDownTimer()


    },
    digitConverter(Num){
      Num = Num.replace(/[٠-٩]/g, d => "٠١٢٣٤٥٦٧٨٩".indexOf(d)).replace(/[۰-۹]/g, d => "۰۱۲۳۴۵۶۷۸۹".indexOf(d));
      return Num;
    }
  },

  created() {
    this.countDownTimer()
  }
}
